import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'react-bootstrap';


class WelcomeBanner extends Component {

    render() {
        return (
            <div>
                <Col xs={12} className='text-left'>
                    <Row className='text-center'>
                        <b>Welcome to Cognitive Concierge!</b> To get started, read each section below for information on
                        how to navigate the site.
                    </Row>
                    <hr/>
                    <Row>
                        <i><b>Chat:</b></i> The Chat functionality is central to the operation of our website. In order
                        to begin a conversation, click the Chat button found below. Our artificial intelligence bot will
                        walk you through steps needed in order to guarantee you receive the best vacation destination
                        recommendation as possible.
                    </Row>
                </Col>
            </div>
        );
    }
}

WelcomeBanner.propTypes = {
    title: PropTypes.string
};
WelcomeBanner.defaultProps = {
    title: ''
};

export default WelcomeBanner;



