import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Row, Button, Panel } from 'react-bootstrap';
import { ChatFeed, Message } from 'react-chat-ui'

import ChatTextEntry from "./chatTextEntry";
import {sendPostRequest} from '../common/api'
import SocialMediaPopover from "../socialMedia/socialMediaPopover";

class ChatDialog extends Component {

    constructor() {
        super();

        this.state = {
            message_list: [],
            waiting_for_machine_response: false,
            conversation_id: "",
            session_id: null,
            social_media: false
        };

        this.newMessage = this.newMessage.bind(this);
        this.popMessage = this.popMessage.bind(this);
        this.getResponse = this.getResponse.bind(this);
        this.addToMsgList = this.addToMsgList.bind(this);
    }

    componentDidMount(){
        this.getResponse(null);
    }

    addToMsgList(text, id){
        let msg_list = this.state.message_list;
        msg_list.push(new Message({id: id, message: text}));
        this.setState({message_list: msg_list});
    }

    popMessage(){
        let msg_list = this.state.message_list;
        if (msg_list.length > 0)
            msg_list.pop();
        this.setState({message_list: msg_list});
    }

    newMessage(text){
        this.addToMsgList(text, 0);
        this.getResponse(text);
    }

    getResponse(msg){
        this.addToMsgList('...', 1);
        let self = this;
        let body = {
            session_token: this.state.conversation_id,
            input: msg,
        };
        sendPostRequest('/conversation', body,
            function(resp){
                self.setState({conversation_id: resp.data.session_token});
                self.popMessage();
                if (resp.data.response)
                    for(let i=0; i<resp.data.response.length; i++)
                        self.addToMsgList(resp.data.response[i], 1);
                if (!self.state.session_id) {
                    self.setState({session_id: resp.data.session_id});
                    self.props.sessionIdUpdateFunction(resp.data.session_id);
                }

                let input = document.getElementById('chatInput');
                input.scrollIntoView(false);
            }, null);

    }

    render() {
        return (
            <Panel style={{width: '100%', minHeight: '50em'}}>
                <Panel.Heading>
                    <Row className='text-center'>
                        <Col xs={6}>
                            You are currently chatting with our AI
                        </Col>
                        <Col xs={3}>
                            <Button style={{width: '100%', textOverflow: 'hidden', overflow: 'hidden'}} onClick={() => {this.setState({social_media: !this.state.social_media})}}>Social</Button>
                        </Col>
                        <Col xs={3}>
                            <Button style={{width: '100%', textOverflow: 'hidden', overflow: 'hidden'}} onClick={this.props.toggleChatFunction} bsStyle={'danger'}>
                                End
                            </Button>
                        </Col>
                    </Row>
                </Panel.Heading>
                <Panel.Body>
                    <Col xs={12} className='pre-scrollable text-center' style={{width: '100%', minHeight: '45em'}}>
                        <Row>
                            <ChatFeed
                                messages={this.state.message_list} // Boolean: list of message objects
                                hasInputField={false} // Boolean: use our input, or use your own
                            />
                        </Row>
                        <Row id={'chatInput'} style={{backgroundColor: 'blue', padding: '3%', borderRadius: '500px'}}>
                            <ChatTextEntry
                                newMessageFunction={this.newMessage}
                                disabled={this.state.waiting_for_machine_response}
                            />
                        </Row>
                    </Col>
                </Panel.Body>
                {this.state.social_media ? <SocialMediaPopover session_id={this.state.session_id}/> : null}
            </Panel>
        );
    }
}

ChatDialog.propTypes = {
    toggleChatFunction: PropTypes.required,
    sessionIdUpdateFunction: PropTypes.func
};
export default ChatDialog;



