import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Tabs, Tab } from 'react-bootstrap';

import Recommendation from '../recommendations/recommendation'

const CONTINENTS = ['africa', 'europe', 'asia', 'north_america', 'south_america', 'australia'];
const CONT_PICS = {'africa': require('../resources/africa.jpg'),
                    'europe': require('../resources/europe.jpg'),
                    'asia': require('../resources/asia.jpg'),
                    'north_america': require('../resources/north_america.jpg'),
                    'south_america': require('../resources/south_america.jpg'),
                    'australia': require('../resources/australia.jpg')};

class RecommendationsHome extends Component {

    constructor(){
        super();

        this.state = {
            all_recommendations: []
        };

        this.getRecommendations = this.getRecommendations.bind(this);
    }

    componentDidMount(){
        if (this.props.recommendations)
            this.setState({all_recommendations: this.props.recommendations});
    }

    componentWillReceiveProps(nextProps){
        if (nextProps.recommendations)
            this.setState({all_recommendations: nextProps.recommendations});
    }

    getRecommendations(continent){
        let MAX = 5;
        let recs = [];

        if (!this.state.all_recommendations.hasOwnProperty(continent)) {
            console.warn("Unknown continent: " + continent);
            return recs;
        }

        for (let i=0; i<this.state.all_recommendations[continent].match_count && i<MAX; i++){
            recs.push(
                <Recommendation key={'recs-' + i} recs={this.state.all_recommendations[continent].matches[i]} image={CONT_PICS[continent]}/>
            )
        }
        if (recs.length < 1)
            console.log("No recommendations found for: " + continent);
        return recs;
    }

    getRecommendationTabs(){
        let tabs = [];

        for(let i=0; i<CONTINENTS.length; i++){
            let curr_cont = CONTINENTS[i];
            let recs = this.getRecommendations(curr_cont);
            tabs.push(
                <Tab eventKey={i} title={curr_cont.replace('_', ' ')} disabled={recs.length < 1}>
                    {recs}
                </Tab>
            )
        }
        return tabs;

    }

    render() {
        return (
            <Col xs={12} className='text-center'>
                <Tabs id={'recommendations-tab'}>
                    {this.getRecommendationTabs()}
                </Tabs>
            </Col>
        );
    }
}

RecommendationsHome.propTypes = {
    recommendations: PropTypes.any
};
RecommendationsHome.defaultProps = {
    recommendations: []
};
export default RecommendationsHome;



