import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Image, Button } from 'react-bootstrap';
import DetailsModal from "./detailsModal";
import ItineraryModal from './itineraryModal';

function progressBarColor(confidence) {
    var bsStyle = "";
    if(confidence < .05) {
      bsStyle = "red";
    }
    else if (confidence < .25) {
      bsStyle = "orange";
    }
    else if (confidence < .35) {
      bsStyle = "yellow"
    }
    else {
      bsStyle = "green";
    }
    bsStyle = 'green'; // @TODO
    return bsStyle;
}

class Recommendation extends Component {
    constructor(props) {
      super(props);
      this.state = {
        recommendations: null,
        show_details_modal: false,
        image: null
      };
    }
    componentDidMount(){
      this.setState({recommendations: this.props.recs, image: this.props.image});
    }
    componentWillReceiveProps(nextProps){
      this.setState({recommendations: nextProps.recs, image: nextProps.image});
    }
    render() {
        if (!this.state.recommendations)
          return null;
        return (
          <div style={{borderStyle: "solid", borderWidth: "3px",
                        marginBottom: "10px", borderRadius: "5px",
                        borderColor: "#696969",
                        backgroundColor: "#e0e0eb"}}>
            <Row style={{height: '15em', overflow: 'hidden', textOverflow: 'hidden'}}>
                <Col xs={1}>
                  <div style={{height: '15em', width:'75%', backgroundColor: progressBarColor(this.state.recommendations.confidence)}}/>
                </Col>
                <Col xs={4} style={{overflow: 'hidden', textOverflow: 'hidden'}}>
                    <Row>
                      <Image src={this.state.image} align="left"
                        style={{ height: "160px", margin: "10px"}}
                        rounded/>
                    </Row>
                    <Row>
                        <Button style={{width: '50%'}} onClick={() => this.setState({show_details_modal: true, show_itinerary_modal: false})}>Details</Button>
                        <Button style={{width: '50%'}} onClick={() => this.setState({show_details_modal: false, show_itinerary_modal: true})}>Generate POIs</Button>
                    </Row>
                </Col>
                <Col xs={7} style={{overflow: 'hidden', textOverflow: 'hidden'}}>
                  <div>
                    <div style={{textAlign: "left", textOverflow: 'hidden'}}>
                      <p style={{fontSize: "20px", marginTop: "4px"}}><strong>{this.state.recommendations.location}</strong></p>
                      <p style={{fontSize: "16px", marginTop: "4px"}}>{this.state.recommendations.description}</p>
                    </div>
                  </div>
                </Col>
            </Row>
            <DetailsModal show={this.state.show_details_modal}
                          close_func={() => this.setState({show_details_modal: false})}
                          recommendation={this.state.recommendations}/>
            <ItineraryModal show={this.state.show_itinerary_modal}
                    close_func={() => this.setState({show_itinerary_modal: false})}
                    recommendation={this.state.recommendations}/>
          </div>
        );
    }
}
Recommendation.propTypes = {
    recs: PropTypes.any,
    image: PropTypes.any
};
export default Recommendation;



