import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Modal, Button, Row, Image } from 'react-bootstrap';
import ScrollArea from 'react-scrollbar';

class DetailsModal extends Component {

    constructor(){
        super();

        this.state = {
            show: false
        };
    }

    componentDidMount(){
        this.setState({
            show: this.props.show
        });
    }

    componentWillReceiveProps(nextProps){
        this.setState({
            show: nextProps.show
        });
    }

    onClose(){
        this.props.close_func();
        this.setState({show: false});

    }

        render() {
            return (
                <Modal show={this.state.show} onHide={this.onClose.bind(this)}>
                    <Modal.Header closeButton>
                        <Modal.Title>{this.props.recommendation.location}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Row style={{padding: '5%'}}>
                            <Col xs={12}>
                                <Row>
                                    <ScrollArea
                                        speed={0.8}
                                        className="area"
                                        contentClassName="content"
                                        horizontal={false}
                                        style={{height: '300px'}}
                                    >
                                        <div>
                                            {this.props.recommendation.description}
                                        </div>
                                    </ScrollArea>
                                </Row>
                            </Col>
                        </Row>
                    </Modal.Body>
                </Modal>
        );
    }
}

DetailsModal.propTypes = {
    show: PropTypes.bool,
    close_func: PropTypes.func,
    recommendation: PropTypes.any

};
DetailsModal.defaultProps = {
    show: false,
    close_func: {},
    recommendation: null

};
export default DetailsModal;



