import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Row, Image } from 'react-bootstrap';
import TwitterModal from './twitterModal'

import tw_logo from '../resources/tw-logo.png'
import {sendPostRequest} from "../common/api";

class TwitterSummary extends Component {

    constructor(){
        super();

        this.state = {
            concepts: [],
            interval: null,
            show_modal: false
        }
    }

    componentDidMount(){
        this.setState({
            session_id: this.props.session_id
        }, function(){
            if (this.state.session_id && this.state.session_id !== '')
            {
                console.log("Registering Twitter updates to timer");
                if (this.interval)
                    clearInterval(this.interval);
                this.interval = setInterval(this.update.bind(this), 5000);
            }
        })
    }


    componentWillReceiveProps(nextProps){
        if (nextProps.session_id === this.state.session_id)
            return;
        this.setState({
            session_id: nextProps.session_id,
            concepts: []
        }, function(){
            if (this.state.session_id && this.state.session_id !== '')
            {
                console.log("Registering Twitter updates to timer");
                if (this.interval)
                    clearInterval(this.interval);
                this.interval = setInterval(this.update.bind(this), 5000);
            }
        })
    }

    update(){
        console.log("Sending request for twitter updates");
        let body={
            session_id: this.state.session_id
        };
        let self = this;
        sendPostRequest('/twitterresults', body,
            function(resp){
                self.setState({
                    concepts: resp.data.concepts,
                    keywords: resp.data.keywords,
                    entities: resp.data.entities
                });
                if (self.interval && resp.data.concepts.length > 0)
                    clearInterval(self.interval);


            }, null);
    }

    render() {
        return (
            <div style={{borderStyle: "solid", borderWidth: "3px",
                margin: "10px", borderRadius: "5px",
                borderColor: "#696969",
                backgroundColor: "#e0e0eb"}}
                onClick={() => this.setState({show_modal: !this.state.show_modal && this.state.concepts.length > 0})}>
                <Row style={{height: '10em', textOverflow: 'hidden'}}>
                    <Col xs={4}>
                        <Row style={{padding: '5px'}}>
                            <Image src={tw_logo} align="center"
                                   style={{ maxHeight: "128px", marginLeft: "3px", opacity: this.state.concepts.length > 0 ? 1 : 0.2}}
                                   rounded
                            />
                        </Row>
                    </Col>
                    <Col xs={7}>
                        <div>
                            <div style={{textAlign: "left", textOverflow: 'hidden'}}>
                                <p style={{fontSize: "20px", marginTop: "4px"}}><strong>Twitter Results</strong></p>
                                <p style={{fontSize: "16px", marginTop: "4px"}}>{this.state.concepts.length > 0 ?
                                    'Click to see the results from your twitter query.' :
                                    'DISABLED - Log into your Twitter account using the social media button inside the chat dialog.'}</p>
                            </div>
                        </div>
                    </Col>
                </Row>
                <TwitterModal show={this.state.show_modal} close_func={() => this.setState({show_modal: false})}
                               concepts={this.state.concepts} keywords={this.state.keywords} entities={this.state.entities}/>
            </div>
        );
    }
}

TwitterSummary.propTypes = {
    session_id: PropTypes.any
};
TwitterSummary.defaultProps = {
    session_id: null
};
export default TwitterSummary;



