import axios from 'axios'

let Config = require('../resources/config.dev');

axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.withCredentials = false;
axios.defaults.crossDomain = false;

export function sendGetRequest(path, cbFunction, cbArgs, resend = false) {
    console.log('Sending Get request to ' + path);
    let full_path = Config.dataServer + path;
    axios.get(full_path)
        .then(function(resp){
            console.log("Received response from GET " + full_path);
            cbFunction(resp, cbArgs);
        })
        .catch(function(err){
            console.log("Failed request " + full_path);
            console.log(err);
            if (resend)
                sendGetRequest(path, cbFunction, cbArgs, false)
            else
                cbFunction(false, cbArgs);
        });
    console.log("Successfully sent GET request to " + full_path);
}

export function sendPostRequest(path, body, cbFunction, cbArgs){
    console.log('Sending POST request to ' + path);
    let full_path = Config.dataServer + path;
    const config = {
        method: 'post',
        url: full_path,
        data: body,
        headers: {
            'Access-Control-Allow-Origin': '*',
            'Content-Type': 'application/json',
        },
    };
    axios.request(config)
        .then(function(resp){
            console.log("Received response from POST " + full_path);
            console.log(resp);
            cbFunction(resp, cbArgs);
        })
        .catch(function(err){
            console.log("Failed request " + full_path);
            console.log(err);
        });
    console.log("Successfully sent POST request to " + full_path);
}


