import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Row, Image } from 'react-bootstrap';

import icon from '../resources/icon.png'

class ChatAd extends Component {

    render() {
        return (
            <div onClick={this.props.toggleChatFunction}>
                <Row style={{backgroundColor: 'tan', border: '1px solid black'}}>
                    <Col xs={3}>
                        <Image src={icon} responsive/>
                    </Col>
                    <Col xs={9} className='text-center'>
                        <h2 stlye={{color: 'brown'}}>Enter Cognitive Chat</h2>
                        <i>Begin our cognitive concierge chat service with out intelligent AI.</i>
                    </Col>
                </Row>
            </div>
        );
    }
}

ChatAd.propTypes = {
    toggleChatFunction: PropTypes.any
};
export default ChatAd;



