import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Modal, Button, Row, Image, ListGroup, ListGroupItem } from 'react-bootstrap';
import ScrollArea from 'react-scrollbar';
import {sendGetRequest} from "../common/api";
import {ClipLoader} from "react-spinners";

class ItineraryModal extends Component {

    constructor(){
        super();

        this.state = {
            show: false,
            poi: [],
            loading: false,
            error: false
        };
    }

    componentDidMount(){
        this.setState({
            show: this.props.show
        });
    }

    componentWillReceiveProps(nextProps){
        if (nextProps.show && !this.state.show)
            this.update_pois();

        this.setState({
            show: nextProps.show
        });
    }

    update_pois(){
        this.setState({loading: true});
        let self = this;
        sendGetRequest('https://api.sandbox.amadeus.com/v1.2/points-of-interest/yapq-search-text?apikey=ztEBAvU6uMo9zUHjqKxzv4sYhAp5ee94&city_name='
            + this.props.recommendation.location.trim(), function(resp){
            let interests = [];
            if (resp === false){
                self.setState({error: true, loading: false});
                return;
            }
            if (resp.data.hasOwnProperty("points_of_interest")) {
                for (let i=0; i<resp.data.points_of_interest.length; i++) {
                    const location = resp.data.points_of_interest[i];
                    console.log(location);
                    interests.push({
                        name: location.title,
                        description: location.details.short_description,
                        wiki: location.details.wiki_page_link,
                        map: location.location.google_maps_link,
                        image: location.main_image
                    });
                }
                self.setState({poi: interests, error: false});
            }
            else {
                console.error("Points of Interest response does not include points of interest.");
            }
            self.setState({loading: false});
        }, null, true)
    }

    onClose(){
        this.props.close_func();
        this.setState({show: false});

    }

    open_link(url){
        console.log("Opening external page " + url);
        window.open(url, '_blank');
    }

    get_poi_cells(){
        let cells = [];
        for (let i=0; i<this.state.poi.length; i++) {
            const poi = this.state.poi[i];
            cells.push(<ListGroupItem header={poi.name} onClick={() => this.open_link(poi.wiki)}>
                <Row style={{minWidth: '100%'}}>
                    <Col xs={3}>
                        <Image src={poi.image} align="left"
                               style={{ height: "100px", maxWidth: '100px', margin: "0"}}
                               circle />
                    </Col>
                    <Col xs={9}>
                        Description: {poi.description}
                    </Col>
                </Row>
            </ListGroupItem>)
        }
        return cells
    }

    get_error(){
        if (!this.state.error)
            return null;

        return <h1>Error: Location not supported.</h1>
    }

    render() {
        return (
            <Modal show={this.state.show} onHide={this.onClose.bind(this)}>
                <Modal.Header closeButton>
                    <Modal.Title>{"Points of interest in " + this.props.recommendation.location}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {this.get_error()}
                    <Row className='text-center'>
                        <ClipLoader
                            color={'#123abc'}
                            loading={this.state.loading}
                        />
                        {this.state.loading ? <i>Loading Points of Interest. This may take a few moments....</i> : null}
                    </Row>
                    <ListGroup>
                         {this.get_poi_cells()}
                    </ListGroup>
                </Modal.Body>
            </Modal>
        );
    }
}

ItineraryModal.propTypes = {
    show: PropTypes.bool,
    close_func: PropTypes.func,
    recommendation: PropTypes.any

};
ItineraryModal.defaultProps = {
    show: false,
    close_func: {},
    recommendation: null

};
export default ItineraryModal;



