import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, PageHeader }  from 'react-bootstrap';

import ChatDialog from './chatDialog'
import ChatAd from './chatAd'

class Chat extends Component {

    constructor(){
        super();

        this.state = {
            showChat: false,
            session_id: null
        };

        this.interval = null;

        this.toggleDialog = this.toggleDialog.bind(this);
        this.updateSession = this.updateSession.bind(this);
        this.updateMatches = this.updateMatches.bind(this);
    }

    toggleDialog() {
        this.setState({
            showChat: !this.state.showChat,
            session_id: null
        });
        clearInterval(this.interval);
        this.interval = null;
    }

    updateSession(id){
        console.log("Setting session id to: " + id);
        this.setState({session_id: id});

        if (this.interval)
            clearInterval(this.interval);
        this.interval = setInterval(this.updateMatches, 5000);
    }

    updateMatches(){
        this.props.updateMatchesFunction(this.state.session_id);
    }

    render() {
        return (
            <div>
                <Row className='text-center'>
                    <PageHeader>Chat <small>Talk with our bot.</small></PageHeader>
                </Row>
                <Row>
                    <Col xs={8} xsOffset={2}>
                        { this.state.showChat ? <ChatDialog toggleChatFunction={this.toggleDialog} sessionIdUpdateFunction={this.updateSession}/>
                            : <ChatAd toggleChatFunction={this.toggleDialog}/> }
                    </Col>
                </Row>
            </div>
        );
    }
}
ChatDialog.propTypes = {
    updateMatchesFunction: PropTypes.func
};
export default Chat;



