import React, { Component } from 'react';
import { Col, Row, PageHeader } from 'react-bootstrap';
import { ClipLoader } from 'react-spinners';

import Chat from '../chat/chat'
import RecommendationsHome from '../recommendations/recommendationsHome'
import {sendPostRequest} from "../common/api";
import SocialMediaHome from "../socialMedia/socialMediaHome";

class DataHome extends Component {

    // Timer for recomendations will sit here
    constructor(){
        super();

        this.state = {
            vacation_recommendations_from_server: [],
            loading: false,
            session_id: null
        };

        this.updateMatches = this.updateMatches.bind(this);
    }

    updateMatches(id){
        this.setState({loading: true, session_id: id});
        console.log("Updating matches...");
        let body = {
            session_id: id
        };
        let self = this;
        sendPostRequest('/match', body,
            function(resp){
                for (let i=0; i<resp.data.match_count; i++){
                    if (!resp.data.matches[i].picture_url || resp.data.matches[i].picture_url.length < 1 ) {
                        resp.data.matches[i].picture_url = 'https://www.freevector.com/uploads/vector/preview/6318/FreeVector-New-York-Skyline-1.jpg';
                    }
                }
                self.setState({
                    vacation_recommendations_from_server: resp.data,
                    loading: false
                })
            }, null);

    }

    render() {
        // This is where you will pass this.state.vacation_recommendations_from_server to your object
        return (
            <div style={{backgroundColor: "#c2c2d6"}}>
                <Col xs={12} md={6}>
                    <Chat updateMatchesFunction={this.updateMatches}/>
                </Col>
                <Col xs={12} md={6}>
                    <PageHeader>Recommendations
                            <ClipLoader
                                color={'#123abc'}
                                loading={false}
                            />
                    </PageHeader>
                    <Row>
                        <RecommendationsHome recommendations={this.state.vacation_recommendations_from_server}/>
                    </Row>

                    <PageHeader>Social Media <small>See the results from social media inquiries</small></PageHeader>
                    <Row>
                        <SocialMediaHome session_id={this.state.session_id}/>
                    </Row>

                </Col>
            </div>
        );
    }
}

export default DataHome;



