import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Modal, Button, Row, Image, PageHeader } from 'react-bootstrap';
import ScrollArea from 'react-scrollbar';

import fb_long from '../resources/fb_long.jpg'

class FacebookModal extends Component {

    constructor(){
        super();

        this.state = {
            show: false,
            concepts: [],
            keywords: [],
            entities: []
        };
    }

    componentDidMount(){
        this.setState({
            show: this.props.show,
            concepts: this.props.concepts,
            keywords: this.props.keywords,
            entities: this.props.entities
        });
    }

    componentWillReceiveProps(nextProps){
        this.setState({
            show: nextProps.show,
            concepts: nextProps.concepts,
            keywords: nextProps.keywords,
            entities: nextProps.entities
        });
    }

    onClose(){
        this.props.close_func();
        this.setState({show: false});
    }

    list_words(words){
        if (!words)
            return null;
        let rows = [];
        for (let i=0; i<words.length; i++)
        {
            rows.push(<Row key={'fb-concepts-' + i}>
                <b>{words[i]}</b>
            </Row>)
        }
        return rows;
    }

        render() {
            return (
                <Modal show={this.state.show} onHide={this.onClose.bind(this)} bsSize={'bsSmall'}>
                    <Modal.Header closeButton>
                        <Modal.Title>Facebook</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Row className='text-center'>
                            <Image src={fb_long}
                                   style={{ width: "90%", height:'250px', margin: "5%", display:'block'}}
                                   rounded />
                        </Row>
                        <Row>
                            <Col xs={12} className='text-center'>
                                <Row className='text-center'>

                                </Row>
                                <Row>
                                    <PageHeader><i>Concepts</i></PageHeader>
                                </Row>
                                <Row>
                                    {this.list_words(this.state.concepts)}
                                </Row>
                                <Row>
                                    <PageHeader><i>Keywords</i></PageHeader>
                                </Row>
                                <Row>
                                    {this.list_words(this.state.keywords)}
                                </Row>
                                <Row>
                                    <PageHeader><i>Entities</i></PageHeader>
                                </Row>
                                <Row>
                                    {this.list_words(this.state.entities)}
                                </Row>
                            </Col>
                        </Row>
                    </Modal.Body>
                </Modal>
        );
    }
}

FacebookModal.propTypes = {
    show: PropTypes.bool,
    close_func: PropTypes.func,
    concepts: PropTypes.any,
    keywords: PropTypes.any,
    entities: PropTypes.any,
};
FacebookModal.defaultProps = {
    show: false,
    close_func: {}

};
export default FacebookModal;



