import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col } from 'react-bootstrap';
import FacebookSummary from "./FacebookSummary";
import TwitterSummary from  "./twitterSummary";

class SocialMediaHome extends Component {
    constructor(){
        super();

        this.state = {
            session_id: null,
            facebook: null,
            twitter: null
        };
    }

    componentDidMount(){
        if (this.props.session_id)
            this.setState({session_id: this.props.session_id});
    }

    componentWillReceiveProps(nextProps){
        if (nextProps.session_id)
            this.setState({session_id: nextProps.session_id});
    }

    render() {
        return (
            <Col xs={12}>
                <FacebookSummary session_id={this.state.session_id}/>

                {/*<TwitterSummary session_id={this.state.session_id}/>*/}
            </Col>
        );
    }
}

SocialMediaHome.propTypes = {

};
SocialMediaHome.defaultProps = {

};
export default SocialMediaHome;



