import React, { Component } from 'react';
import { Col, Button } from 'react-bootstrap'
import {sendPostRequest} from "../common/api";

import Banner from '../banner/banner'
import DataHome from './dataHome'

class Home extends Component {

    debug(){
        console.log("Sending debug request...");
        sendPostRequest()

    }

    render() {
        return (
            <Col xs={12} style={{backgroundColor: "#c2c2d6", minHeight: "100vh"}}>
                <Banner
                    title={'Cognitive Concierge Demo'}
                />
                <div>
                    <DataHome/>
                </div>
            </Col>
        );
    }
}

Home.propTypes = {

};
Home.defaultProps = {

};
export default Home;



