import React, { Component } from 'react';
import { Carousel, Col } from 'react-bootstrap';

import nyc from '../resources/nyc.jpg'
import rockies from '../resources/rocky.jpg'
import geneva from '../resources/geneva.jpg'

let IMAGES = [{
        image: nyc,
        label: "New York City, USA",
        description: "Major city located on the East Coast within the United States."
    },
    {
        image: rockies,
        label: "Rocky Mountains, USA",
        description: "Famous, large mountain range located in Western United States."
    },
    {
        image: geneva,
        label: "Lake Geneva",
        description: "Large lake and castle located within central Switzerland."
    }];

class CarouselBanner extends Component {

    getImages(){
        let images = [];
        for(let i=0; i<IMAGES.length; i++) {
            let curr_img = IMAGES[i];
            images.push(
                <Carousel.Item key={'carousel-' + i}>
                    <img width={1920/3} height={1080/3} className='img-responsive center-block'
                         alt={'1920x1080'} src={curr_img.image}/>
                    <Carousel.Caption>
                        <h3>{curr_img.label}</h3>
                        <p>{curr_img.description}</p>
                    </Carousel.Caption>
                </Carousel.Item>
            );
        }
        return images;
    }

    render() {
        return (
            <Col xs={12} className='text-center'>
                <Carousel style={{margin: 'auto'}} active={'true'}>
                    {this.getImages()}
                </Carousel>
            </Col>
        );
    }
}


export default CarouselBanner;



