import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Popover, Row } from 'react-bootstrap';
import FacebookLogin from 'react-facebook-login';
import TwitterLogin from 'react-twitter-auth/lib/react-twitter-auth-component.js';

import {sendPostRequest} from "../common/api";


class SocialMediaPopover extends Component {

    facebookCallback(resp){
        console.log("Received login response from facebook.");
        console.log("Sending facebook media request with\ntoken: " + resp.accessToken + "\nSession Id: " + this.props.session_id);
        let body={
            token: resp.accessToken,
            session_id: this.props.session_id
        };
        let self = this;
        sendPostRequest('/analyzefacebook', body,
            function(resp){
                console.log("Successfully send facebook token to backend.");
                self.onRequestHide()
            }, null);
    }

      onFailed = (error) => {
          alert(error);
      };
      twitterCallBack(resp){
        console.log("Received login response from twitter.");
        console.log("Sending twitter media request with\ntoken: " + resp.accessToken + "\nSession Id: " + this.props.session_id);
        let body={
            token: resp.accessToken,
            session_id: this.props.session_id
        };
        let self = this;
        sendPostRequest('/analyzetwitter', body,
            function(resp){
                console.log("Successfully send twitter token to backend.");
                self.onRequestHide()
            }, null);    }
    render() {
        return (
            <Popover
                id="popover-basic"
                placement="bottom"
                positionLeft={100}
                positionTop={50}
                title="Social Media Login"
                style={{maxWidth: '100%', padding:'2%'}}
            >
                <Col xs={12}>
                    <Row className='text-center'>
                        <h2>Social Media Login:</h2>
                    </Row>
                    <hr/>
                    <Row>
                        <FacebookLogin
                            appId="593621317649427"
                            autoLoad
                            callback={this.facebookCallback.bind(this)}
                            fields="name,email,picture"
                            scope='user_posts'
                        />

                    </Row>
                    {/*
                    <Row>
                    <TwitterLogin loginUrl="http://localhost:8000/api/auth/twitter"
                          onFailure={this.onFailed.bind(this)}
                          onSuccess={this.twitterCallBack.bind(this)}
                          requestTokenUrl="http://localhost:8000/api/auth/twitter/reverse"
                          showIcon={true}
                          customHeaders={[]}/>
                    </Row>
                    */}

                </Col>
            </Popover>
        );
    }
}

SocialMediaPopover.propTypes = {
    session_id: PropTypes.string

};
SocialMediaPopover.defaultProps = {

};
export default SocialMediaPopover;



