import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, FormGroup, FormControl, Button } from 'react-bootstrap';

class ChatTextEntry extends Component {

    constructor() {
        super();

        this.state = {
            value: '',
            disabled: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.interceptEnterKey = this.interceptEnterKey.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
    }

    componentDidMount(){
        this.setState({disabled: this.props.disabled});
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ disabled: nextProps.disabled });
    }

    handleChange(e){
        this.setState({
            value: e.target.value
        });
    }

    interceptEnterKey(e) {
        if (e.key === 'Enter') {
            e.preventDefault();
            this.sendMessage();
        }
    }

    sendMessage(){
        console.log("Submitting user message: " + this.state.value);
        if (this.props.newMessageFunction)
            this.props.newMessageFunction(this.state.value);
        this.setState({value: ''});
    }

    render() {
        return (
            <form onKeyDown={this.interceptEnterKey}>
                <Col xs={8}>
                  <FormGroup>
                        <FormControl
                            type={this.props.password ? "password" : "text"}
                            value={this.state.value}
                            placeholder="Response"
                            onChange={this.handleChange}
                            disabled={this.state.disabled}
                        />
                    </FormGroup>
                </Col>
                <Col xs={4} className='text-left'>
                    <Button onClick={this.sendMessage} disabled={this.state.disabled}>Send</Button>
                </Col>
            </form>
        );
    }
}

ChatTextEntry.propTypes = {
    password: PropTypes.bool,
    newMessageFunction: PropTypes.any,
    disabled: PropTypes.bool
};
ChatTextEntry.defaultProps = {
    password: false,
    newMesageFunction: null,
    disabled: false
};
export default ChatTextEntry;



